  background: linear-gradient(45deg, #336085, #287076, #5b1e5e, #6d226c);
    animation: gradientAnimation 20s infinite;
    background-size: 600% 600%;
}

.newsblock {
  &::-webkit-scrollbar {
    @apply w-[10px];
  }
  &::-webkit-scrollbar-track {
    @apply bg-[#f1f1f100];
  }
  &::-webkit-scrollbar-thumb {
    @apply bg-white rounded-[20px];
  }
