<script setup>
import { ref, onMounted  } from 'vue';

// Supabase
import { supabase } from '../clients/supabase';

// Swiper js
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/css';
import 'swiper/css/effect-cards';
import 'swiper/css/effect-fade';
import 'swiper/css/pagination';
import 'swiper/css/effect-coverflow';
import { EffectCards, EffectFade, Pagination, Autoplay, EffectCoverflow } from 'swiper/modules';

// Components
import ContactForm from '@/components/ContactForm.vue';
import SponsorsComp from '@/components/Sponsors.vue';

const modules = [EffectCards, EffectFade, Pagination, Autoplay, EffectCoverflow];
const match1value = ref("");
const match2value = ref("");
const datevalue = ref("");
const locationvalue = ref("");
const news_headline = ref("");
const copyrightyear = getYear();

const navlinks = [
{ name: "Home", url: "#" },
{ name: "News", url: "#news" },
{ name: "Committee", url: "#committee" },
{ name: "Teams", url: "#teams" },
{ name: "Policies", url: "#policies" },
]

const teams = [
{ manager: "Craig Duff", teamname: "U8's", description: "This is our U8's. Managed by Craig Duff with assisted coaching from Tom Bearne." }, // 9
{ manager: "Jamie Munro", teamname: "U8 Trojans", description: "This is our U8 Trojans. Managed by Jamie Munro with assisted coaching from Paul Shepard and Mike Carrasco."}, // 2

{ manager: "Barry Munro", teamname: "U9 Predators", description: "This is our U9 Predators. Managed by Barry Munro with assisted coaching from David tucker." }, // 1

{ manager: "Alan Gowland", teamname: "U10 Colts", description: "This is our U10 Colts. Managed by Alan Gowland with assisted coaching from Phil Lerendu." }, // 6
{ manager: "Karl Williams", teamname: "U10 Trojans", description: "This is our U10 Trojans. Managed by Karl Williams with assisted coaching from David Grove and Pete Coleman." }, // 7

{ manager: "James Wenman", teamname: "U11 Blues", description: "This is our U11 Blues. Managed by James Wenman with assisted coaching from Sam Stephans." }, // 10

{ manager: "Lee Davis", teamname: "U12's", description: "THis is our U12's. Managed by Lee Davis with assisted coaching from Adam Newman." }, // 8
{ manager: "Dan Butterworth", teamname: "U12 Colts", description: "This is our U12 Colts. Managed by Dan Butterworth with assisted coaching from Steven Courtier, Adam Robertson, and Paul Wood." }, // 3

{ manager: "Luke Asplen", teamname: "U14's", description: "This is our U14's. Managed by Luke Asplen with assisted coaching from Derek Fields and Neville Wrathall." }, // 5
{ manager: "Des Pitman", teamname: "U14 Predators", description: "This is our U14 Predators. Managed by Des Pitman with assisted coaching from Scott Somerset and Chris Watts." }, // 4

{ manager: "John English", teamname: "U15 Tornados", description: "This is our U15 Tornadoes. Managed by John English with assisted coaching from Phil Lerendu and Cavan Kirby." }, // 11
];

function nextslide() {
  const swiper = document.querySelector('.teams-swiper').swiper;
  swiper.slideNext();
}
function prevslide() {
  const swiper = document.querySelector('.teams-swiper').swiper;
  swiper.slidePrev();
}

function getYear() {
  return new Date().getFullYear();
}

function getDate() {
  const collecteddate = document.getElementById('date');
  
  const date = new Date();
  const day = date.getDate();
  const month = date.getMonth() + 1;
  const year = date.getFullYear();
  
  collecteddate.textContent = `${day}/${month}/${year}`;
}

function HideExpandedNews() {
  const expandednews = document.querySelector('.expandednews');
  
  expandednews.style.opacity = "0";
}

async function get_news_db() {
  const newsboxcontainer = document.querySelector('.newsboxcontainer');
  
  let { data: news_table, error } = await supabase
  .from('news_table')
  .select('*');
  
  if (error) {
    console.error('Error fetching data:', error.message);
    return;
  }
  
  news_table.forEach((news, index) => {
    const { news_date, news_heading, news_para, image_url } = news;
    
    if (!news_heading || news_heading.trim() === "null") {
      return;
    }
    
    const newnews = document.createElement('div');
    newnews.classList.add('news_boxes');
    newnews.innerHTML = `
      <img src="${image_url}" class="w-full h-20 sm:h-40 object-cover sm:rounded-t newsimages newimage${index}" data-index="${index}" />

      <p class="text-xs mt-2 mb-2 opacity-80">${news_date}</p>
      <h2 class="sm:mb-3 text-sm sm:text-base font-semibold newsheading">${news_heading}</h2>

      <p class="newsdesc text-xs sm:text-sm mt-5 h-20 opacity-70 overflow-y-scroll">${news_para}</p>

    `;
    
    newnews.querySelector('.newsimages').addEventListener('click', expandnews);
    newsboxcontainer.appendChild(newnews);
    
    const seemorebutton = document.querySelector('#viewallbutton');
    
    // if there are 3 or less news, disable the 'View All' button
    if (newsboxcontainer.querySelectorAll('.news_boxes').length <= 3) {
      seemorebutton.style.display = "none";
    } else {
      seemorebutton.style.display = "block";
    }
  });
}


function expandnews(event) {
  const newsElement = event.currentTarget;
  const newsBox = newsElement.closest('.news_boxes');
  // const newsDesc = newsBox.querySelector('.newsdesc');
  
  // newsDesc.style.display === "block" ? newsDesc.style.display = "none" : newsDesc.style.display = "block";
}

async function get_DB_Values() {
  get_news_db()
  
  const { data, error } = await supabase
  .from('tas_database')
  .select('*')
  .eq('id', 22);
  
  if (error) {
    console.error('Error fetching data:', error);
    return;
  }
  
  const rowData = data[0];
  
  if (!rowData) {
    console.error('No data found for id 22');
    return;
  }
  
  match1value.value = rowData.match_1 || '';
  match2value.value = rowData.match_2 || '';
  news_headline.value = rowData.news || '';
  locationvalue.value = rowData.location || '';
  datevalue.value = rowData.date || '';
}

function expandnav() {
  const [nav, hiddennav] = [document.querySelector(".normalnav"), document.querySelector(".hiddennav")];
  const isExpanded = nav.style.height == "40%";
  
  nav.style.height = isExpanded ? "48px" : "40%";
  hiddennav.style.display = isExpanded ? "none" : "block";
  hiddennav.style.transform = isExpanded ? "translateY(-40px)" : "translateY(0)";
  
  if (!isExpanded) {
    hiddennav.style.opacity = "0";
    setTimeout(() => hiddennav.style.opacity = "1", 150);
  }
}

function showallnews() {
  const newsbox4 = document.querySelector('.news_boxes:nth-child(4)');
  const newsbox5 = document.querySelector('.news_boxes:nth-child(5)');
  const viewallbutton = document.querySelector('#viewallbutton');
  
  // Toggle the display of the next two news boxes
  if (viewallbutton.textContent === 'View all') {
    if (newsbox4) {
      newsbox4.style.display = "block";
    }
    if (newsbox5) {
      newsbox5.style.display = "block";
    }
    
    
    viewallbutton.textContent = "View less";
  } else {
    if (newsbox4) {
      newsbox4.style.display = "none";
    }
    if (newsbox5) {
      newsbox5.style.display = "none";
    }
    
    viewallbutton.textContent = "View all";
  }
}


function ExpandPolicy(currentTarget) {
  // Get the closest policybox to the clicked element
  const policybox = currentTarget.closest('.policyboxes');
  const plusminus = policybox.querySelector('.plusminus');
  const policytext = policybox.querySelector('.policytext');
  
  const isActive = policybox.classList.contains('active');
  
  // Toggle the 'active' class on the specific elements
  policybox.classList.toggle('active');
  plusminus.classList.toggle('active');
  
  // Adjust the height of the policy text for this specific box
  policytext.style.height = isActive ? '0' : `${policytext.scrollHeight}px`;
  policytext.style.marginTop = isActive ? '0' : '15px';
}

onMounted(() => {
  get_DB_Values();
  
  window.onscroll = () => {
    const scrolled = document.body.scrollTop > 2 || document.documentElement.scrollTop > 2;
    document.querySelector('.normalnav').style.height = scrolled ? "38px" : "48px";
    document.querySelector('.tasheader').style.fontSize = scrolled ? "13px" : "18px";
    document.querySelector('.hamburger').classList.toggle('hamburgerscrolled', scrolled);
  };
  
  getDate();
})
</script>

